.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
html {
  font-size: 24px;
  line-height: 2;
}

header {
  width: 100%;
  background-color: ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  nav {
    text-align: center;
    a{
      padding: 20px;
      text-decoration: none;
      color: black;
    }
  }
}
